export default {
    data() {
        return {
            loading: true,
            keyword: '',
            list: {
                items: [],
                page: 1,
                totalPage: 0,
                pageSize: 0
            }
        }
    },

    components: {},

    mounted() {
        this.refresh()
    },

    methods: {
        refresh(page = 1) {
            this.loading = true
            this.$store.dispatch('adviceList', {
                data: {
                    page
                },
                success: data => {
                    this.list = data
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        }
    }
}